.work-thumbnail__project-highlight {
  outline: 2px solid red;
  outline-offset: 4px;
}

.wrapper {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr;
  padding: 8px;
  //animation: 0.1s ease-out 0s 1 appear;

  .loader {
    display: none;
    grid-column: 1 / 2;
    grid-row: 1 / 2;
  }

  &.loading {
    img,
    video {
      // display: none;
      grid-column: 1 / 2;
      grid-row: 1 / 2;
      opacity: 0;
      // transform: translate(0, -20px);
    }

    &:after {
      opacity: 0 !important;
    }

    .loader {
      grid-column: 1 / 2;
      grid-row: 1 / 2;
      display: flex;
      width: 100%;
      height: 100%;
      align-items: center;
      justify-content: center;
      // Need to use inline SVG background for the dashed border
      background-image: url("data:image/svg+xml,%3csvg width='100%25' height='100%25' xmlns='http://www.w3.org/2000/svg'%3e%3crect width='100%25' height='100%25' fill='none' rx='12' ry='12' stroke='rgba(255, 255, 255, 0.2)' stroke-width='2px' stroke-dasharray='4%2c4' stroke-dashoffset='0' stroke-linecap='round'/%3e%3c/svg%3e");
      border-radius: 12px;

      svg {
        stroke-dasharray: 100, 4, 4, 4;
        stroke-dashoffset: 0;
        animation-name: loader-svg;
        animation-duration: 2s;
        animation-iteration-count: infinite;
      }
    }
  }

  @keyframes loader-svg {
    0% {
      stroke-dashoffset: 112;
    }
    100% {
      stroke-dashoffset: 0;
    }
  }

  // Selection states
  &.selected {
    border: 1px solid rgba(255, 255, 255, 0.2);
    background: rgba(255, 255, 255, 0.25);
    padding: 7px;
    border-radius: 16px;
  }

  // &.same-project {
  //   // border: 1px dashed rgba(255, 255, 255, 0.3);
  //   background: rgba(255, 255, 255, 0.1);
  //   padding: 8px;
  //   border-radius: 16px;
  // }

  &.not-selected {
    opacity: 0.5;
    filter: blur(4px);
  }

  .video-wrapper {
    grid-column: 1 / 2;
    grid-row: 1 / 3; // 3 is magic number. Don't know why video has a smaller height while spanning 3 rows...
  }

  img,
  video {
    grid-column: 1 / 2;
    grid-row: 1 / 2;
    width: 100%;
    height: 100%;
    max-height: 100%;
    border-radius: 10px;
    // transform: translate(0, 0);
    transition: all 0.5s ease-in-out;
  }

  img {
    object-fit: cover;

    &:hover {
      //transform: scale(1.1);
    }
  }

  video {
    object-fit: contain;
    object-position: center;

    &:hover {
      //transform: scale(1.1);
    }
  }

  .center {
    object-fit: contain;
  }
}

.small {
  grid-column: span 1;
  grid-row: span 2;
}

.medium {
  grid-column: span 2;
  grid-row: span 4;
}

.wide {
  grid-column: span 2;
  grid-row: span 3;

  @media screen and (max-width: 370px) {
    grid-row: span 4;
  }
}

.tall {
  grid-column: span 1;
  grid-row: span 6;
}

.big {
  grid-column: span 2;
  grid-row: span 6;
}

.wide-big {
  grid-column: span 4;
  grid-row: span 6;

  @media screen and (max-width: 370px) {
    grid-column: span 2;
    grid-row: span 5;
  }
}

.tall-big {
  grid-column: span 2;
  grid-row: span 12;
}

.tall-big-phone {
  grid-column: span 2;
  grid-row: span 8;
}

.video-shadow > .video-wrapper {
  // box-shadow: 0 2px 4px black;
  filter: drop-shadow(0px 1px 2px rgba(0, 0, 0, 0.6));
}

.daily-motion {
  mask-image: url(../../assets/videos/daily-mail-concept-mask-rgb.png);
  mask-size: contain;
  mask-position: center;
}

.latency {
  position: relative;
  padding: 9px;

  .video-wrapper {
    z-index: 1;
  }

  &:after {
    z-index: 0;
    content: '';
    position: absolute;
    top: 8px;
    left: 8px;
    width: calc(100% - 16px);
    height: calc(100% - 16px);
    background-color: white;
    border-radius: 10px;
  }
}

.selected.latency:after {
  top: 7px;
  left: 7px;
  width: calc(100% - 14px);
  height: calc(100% - 14px);
}

.cover-video {
  object-fit: cover !important;
}

.screenshot-fit {
  object-fit: contain !important;
}

// white container for the screenshot â†“

.screenshot-container {
  background-color: white;
  border-radius: 10px;
  padding: 10px;
}

// Animations

@keyframes slideInFromLeft {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(0);
  }
}

@keyframes layDown {
  0% {
    opacity: 0;
    transform: perspective(400px) translate3d(0, -60px, 40px)
      rotate3d(1, 0, 0, 15deg);
  }
  100% {
    opacity: 1;
    transform: perspective(400px) translate3d(0, 0, 0) rotate3d(1, 0, 0, 0);
  }
}

@keyframes appear {
  0% {
    opacity: 0;
    transform: perspective(400px) translate3d(0, 20px, -40px)
      rotate3d(1, 0, 0, -3deg);
  }
  100% {
    opacity: 1;
    transform: perspective(400px) translate3d(0, 0, 0) rotate3d(1, 0, 0, 0);
  }
}

@keyframes pulse {
  0% {
    //background-color: #001F3F;
    transform: scale(1);
  }

  100% {
    //background-color: #FF4136;
    transform: scale(1.2);
  }
}
